import VMultiCascader from './multi-cascader'

const install = function (Vue) {
  // 
  if (install.installed) return
  // 
  Vue.component(VMultiCascader.name, VMultiCascader)
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export {
  //  install Vue.use() 
  install,
  // 
  VMultiCascader
}

export default {
  //  install Vue.use() 
  install,
  // 
  VMultiCascader
}
